const handleChange = () =>{
    var yamlText = window.iagyaml;

    var json = {};

    if(yamlText !== null && yamlText !== undefined) {
        json = safeLoad(yamlText);
        if(json === undefined || json === null) {
          json = {};
        }
    }

    var newElem = {};
    var newhttp_transformations = {};
    newhttp_transformations["postauthn"] = policies_http_transformations_postauthnrowData.policies_http_transformations_postauthn;
    newhttp_transformations["postazn"] = policies_http_transformations_postaznrowData.policies_http_transformations_postazn;
    newhttp_transformations["preazn"] = policies_http_transformations_preaznrowData.policies_http_transformations_preazn;
    newhttp_transformations["request"] = policies_http_transformations_requestrowData.policies_http_transformations_request;
    newhttp_transformations["response"] = policies_http_transformations_responserowData.policies_http_transformations_response;
    newhttp_transformations["secrets"] = policies_http_transformations_secretsrowData.policies_http_transformations_secrets;
    newElem["http_transformations"] = newhttp_transformations;
    newElem["postauthn"] = policies_http_transformations_postauthnrowData.policies_http_transformations_postauthn;
    newElem["authorization"] = policies_authorizationrowData.policies_authorization;
    newElem["postazn"] = policies_http_transformations_postaznrowData.policies_http_transformations_postazn;
    newElem["rate_limiting"] = policies_rate_limitingrowData.policies_rate_limiting;
    newElem["content_injection"] = policies_content_injectionrowData.policies_content_injection;
    newElem["preazn"] = policies_http_transformations_preaznrowData.policies_http_transformations_preazn;
    newElem["request"] = policies_http_transformations_requestrowData.policies_http_transformations_request;
    newElem["response"] = policies_http_transformations_responserowData.policies_http_transformations_response;
    newElem["secrets"] = policies_http_transformations_secretsrowData.policies_http_transformations_secrets;
    newElem["cors"] = policies_corsrowData.policies_cors;
    json["policies"] = newElem;

    var yamlop = jsonToYaml(json);
    window.iagyaml = yamlop;
};

